html {
    box-sizing: border-box;
}

*, :after, :before {
    box-sizing: inherit;
}
::selection {
    color: #fff;
    background: #a51a1a;
}

body {
    background: #f6f6f6;
}

body {
    font: 16px/1.6 Roboto,Helvetica,sans-serif;
    font-weight: 300;
    /* background-color: red */
}
body {
    margin: 0;
}

h1 {
    font-size: 36px;
    font-weight: 400;
    text-transform: uppercase;
    -webkit-font-smoothing: antialiased;
}
h2 {
    font-weight: 400;
    font-size: 30px;
    text-transform: uppercase;
    -webkit-font-smoothing: antialiased;
}
h1, h2, h3, h4, h5, h6 {
    margin: 0;
}
p {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
strong {
    font-weight: 500;
}

a {
    color: #BB1E1E;
    text-decoration: none;
}

a {
    background: 0 0;
}

em {
    font-style: italic
}

hr {
    padding: 0;
    margin: 30px 0;
    height: 1px;
    width: 100%;
    background: #ddd;
    border: none;
}

hr {
    box-sizing: content-box;
}

/* Main Section */
.site-main {
    margin-left: 25%;
}
.site-main .site-main-inner {
    position: relative;
}
.offer-section {
    max-width: 850px;
    padding: 50px;
    background-color: #fff;
    color: #333;
}
.main-title {
    line-height: 1;
    margin-bottom: 15px;
    color: #555;
}

.fa {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    /* -moz-osx-font-smoothing: grayscale; */
}
.red {
    color: #BB1E1E;
}
.row {
    overflow: hidden;
}
.feature {
    color: #BB1E1E;
    font-weight: 300;
    text-align: center;
    padding: 0 10px;
}
.col {
    display: block;
    float: left;
}

.col-4 {
    width: 33.33333%;
}

.feature .feature-number {
    font-size: 48px;
    line-height: 70px;
}
.feature .feature-description {
    margin-top: 10px;
    font-size: 20px;
    line-height: 1.3;
}
.feature .feature-number img {
    display: block;
    margin: 0 auto;
    height: 70px;
    width: 70px;
}

img {
    border: 0;
}